.card {
  font-family: Roboto, Helvetica, sans-serif;
  font-weight: 500;
  font-size: 12px;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-shrink: 0;

  &:not(:nth-child(4n)) {
    margin-right: 48px;
  }

  &__pic {
    margin-top: 32px;
    margin-bottom: 40px;
    height: 134px;
    width: 160px;
  }

  &__product-identification {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 16px;

    .product-name {
      color: #060b35;
      text-decoration: none;
      line-height: 18px;
      margin-bottom: 4px;
    }

    .product-code {
      margin-bottom: 16px;
      font-size: 10px;
      font-weight: 400;
      line-height: 14px;
      color: #616070;
    }
  }

  &__product-rating {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px;

    .reviews-number {
      font-weight: 400;
      font-size: 10px;
      line-height: 14px;
      color: #060b35;
    }
  }

  &__price {
    display: flex;
    margin-bottom: 16px;
    line-height: 18px;

    .price-text {
      margin-right: 55px;
      font-weight: 400;
      color: #616070;
    }

    .price-amount {
      font-weight: 700;
      font-size: 16px;
      color: #060b35;
    }
  }

  &__buy-button {
    display: flex;
    padding: 12px 55px;
    font-weight: 700;
    font-size: 14px;
    border: 1px solid transparent;
    border-radius: 5px;
    text-transform: uppercase;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: white;
    background-color: #00acdc;
    margin-bottom: 16px;

    &:hover {
      background-color: #fff;
      color: #00acdc;
      border-color: #00acdc;
    }
  }
}
